import { CCard, CCardHeader, CCardBody, CCardTitle, CCardText, CRow, CCol } from '@coreui/react'
import PageHeading from '../../components/PageHeading'
import { WhatsappIcon } from 'react-share';

function isMobileOrTablet() {
    return /(android|iphone|ipad|mobile)/i.test(navigator.userAgent);
}

const address = "*Renukari Residency,* Gat no: 62, Plot no: 19, Near 100ft road, Vidyut Colony, Jalgaon 425001"

const Social = () => {

    const shareOnWhatsapp = () => {
        const encodedText = encodeURIComponent(address);
        const url = `https://${isMobileOrTablet ? 'api' : 'web'}.whatsapp.com/send?text=${encodedText}`;
        window.open(url, '_blank');
    }

    const shareLocation = () => {
        const text = "*Renukai Residency Location* : https://goo.gl/maps/YyAXcdHj6ukNS7Kf9"
        const encodedText = encodeURIComponent(text);
        const url = `https://${isMobileOrTablet ? 'api' : 'web'}.whatsapp.com/send?text=${encodedText}`;
        window.open(url, '_blank');
    }
    // const shareOnMessage = () => {
    //     const encodedMessage = encodeURIComponent(address);
    //     const url = `sms:?body=${encodedMessage}`;
    //     window.open(url, '_blank');
    // }

    return <>
        <CCard className="mb-4">
            <CCardHeader>
                <PageHeading>
                    Social
                </PageHeading>
            </CCardHeader>
            <CCardBody>
                <CCard className="text-center">
                    <CCardBody>
                        <CCardTitle>Renukai Residency Address</CCardTitle>
                        <CCardText>
                            {address}
                        </CCardText>
                        <b>Share Address on</b> <br />
                        <WhatsappIcon onClick={shareOnWhatsapp} borderRadius={10} width={40} height={40} />&nbsp;
                        {/* <FacebookMessengerIcon onClick={shareOnFacebook} borderRadius={10} width={40} height={40} />&nbsp; */}
                        {/* <EmailIcon onClick={shareOnMessage} borderRadius={10} width={40} height={40} />&nbsp; */}
                        {/* <CButton onClick={shareOnWhatsapp}>Share</CButton> */}
                    </CCardBody>
                </CCard>
                <CCard className="text-center">
                    <CCardBody>
                        <CCardTitle>Renukai Residency Location</CCardTitle>
                        <CCardText>
                            <CRow>
                                <CCol>
                                    <iframe
                                        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3724.695570812455!2d75.54387491485397!3d21.004836886011333!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bd90f311408818b%3A0xb8d74343af8eb55f!2sRENUKAI%20RESIDENCY!5e0!3m2!1sen!2sin!4v1675928463005!5m2!1sen!2sin"
                                        width="100%" height="100%" style={{ border: 0 }} allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade">
                                    </iframe>
                                </CCol>
                            </CRow>
                        </CCardText>
                        <b>Share Location on</b> <br />
                        <WhatsappIcon onClick={shareLocation} borderRadius={10} width={40} height={40} />&nbsp;
                        {/* <FacebookMessengerIcon onClick={shareOnFacebook} borderRadius={10} width={40} height={40} />&nbsp; */}
                        {/* <EmailIcon onClick={shareOnMessage} borderRadius={10} width={40} height={40} />&nbsp; */}
                        {/* <CButton onClick={shareOnWhatsapp}>Share</CButton> */}
                    </CCardBody>
                </CCard>
            </CCardBody>
        </CCard>
    </>
}

export default Social